import 'bootstrap/dist/css/bootstrap.css';
import document from 'global/document';
import React, { Fragment } from 'react';
import { render, hydrate } from 'react-dom';
import { Provider } from 'react-redux';
import Loadable from 'react-loadable';
// import { createStore, applyMiddleware } from 'redux';
import reduxThunk from 'redux-thunk';
import { BrowserRouter, Switch, Route } from 'react-router-dom';
import { Frontload } from 'react-frontload';
import { ConnectedRouter } from 'connected-react-router';

import createStore from './store';
import './index.scss';
import App from './components/App';
import GoToAppStore from './components/GoToAppStore/GoToAppStore';
import Intercom from './components/Intercom/Intercom';

import reducers from './reducers';

// registerServiceWorker, 
import { unregister } from './registerServiceWorker';

// const store = createStore(reducers, {}, applyMiddleware(reduxThunk));

// Create a store and get back itself and its history object
const { store, history } = createStore();

const Application = (
  <Provider store={store}>
    <ConnectedRouter history={history}>
      <Frontload noServerRender={true}>
        <App />
      </Frontload>
    </ConnectedRouter>
  </Provider>
);

const root = document.querySelector('#root');
// const rootElement = document.getElementById('root');
// const JSX = (
//   <Provider store={store}>
//     <BrowserRouter>
//       <Fragment>
//         {/* <p>Test SEO</p> */}
//         <Switch>
//           {/* <Route exact path="/gotoappstore" component={GoToAppStore} />
//           <Route exact path="/intercom" component={Intercom} /> */}
//           <Route component={App} />
//         </Switch>
//       </Fragment>
//     </BrowserRouter>
//   </Provider>
// );

if (root.hasChildNodes() === true) {
  // If it's an SSR, we use hydrate to get fast page loads by just
  // attaching event listeners after the initial render
  Loadable.preloadReady().then(() => {
    console.log('USING SSR');
    hydrate(Application, root);
  });
} else {
  // If we're not running on the server, just render like normal
  render(Application, root);
}

// render(JSX, rootElement);

// registerServiceWorker();
unregister();
