export const gaTag = (customData) => {
  ReactGA.pageview(customData && customData.page_path, null, customData && customData.page_title);
};

export const gaEvent = (action, customData) => {
  ReactGA.event({
    category: customData && customData.event_category,
    action,
    label: customData && customData.event_label,
  });
};

